.login {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('https://ak.picdn.net/shutterstock/videos/17576434/thumb/5.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    background-position: center;
    text-align: center;
}

form {
    display: flex;
    flex-direction: column;
    color: rgb(255, 255, 255);
    border: 1px solid white;
    padding: 50px;
    font-size: 1.6rem;
}

#username {
 margin-top: 10px;
 height: 30px;
}

label {
    text-transform: uppercase;
}

#submit {
    margin-top: 10px;
    color: white;
    background-color: rgb(122, 153, 238);
    padding: 7px;
    border: 0;
    border-radius: 2px;
}

#submit:hover {
    background-color: aquamarine;
}